<template>
   <div class="mt-2 mb-2 d-flex justify-content-center">
      <form @submit="addTodo">
         <input v-model="title" class="mr-2" placeholder="Add todo..." type="text" name="title" />
         <button class="btn btn-primary" type="submit">Add</button>
      </form>
   </div>
</template>

<script>
export default {
   name: "AddTodo",
   data() {
      return {
         title: ""
      };
   },
   methods: {
      addTodo(e) {
         e.preventDefault();
         const newTodo = {
            title: this.title,
            completed: false
         };
         //Send up to parent
         this.$emit("add-todo", newTodo);
         this.title = "";
      }
   }
};
</script>
