<template>
   <b-row class="mt-2" v-bind:class="{ 'is-complete': todo.completed }">
      <b-col cols="1">
         <input type="checkbox" v-on:change="markComplete" />
      </b-col>
      <b-col>
         <p>
            {{ todo.title }}
         </p>
      </b-col>
      <b-col cols="2">
         <b-button @click="$emit('del-todo', todo.id)" variant="danger">Remove</b-button>
      </b-col>
   </b-row>
</template>

<script>
export default {
   name: "TodoItem",
   props: ["todo"],
   methods: {
      markComplete() {
         this.todo.completed = !this.todo.completed;
      }
   }
};
</script>

<style scoped>
.is-complete {
   text-decoration: line-through;
}
</style>
